.login {
    position :relative;
}
.login::after {
    content : "";
    display: block;
    position: absolute;
    top: 0;
    left : 0;
    background-position: center;
    background-image: url(/static/media/background.ecb28f8a.jpg);
    background-size: cover;
    height: 100%;
    width: 100%;
    opacity:0.8;
    z-index: -1;
}
.collection-create-form_last-form-item {
    margin-bottom: 0;
  }
